@import '../text-input/text-input.scss';
@import '../button/button.scss';

.si-form {
  max-width: 380px;
  padding: 40px 5px 30px;
  display: flex;
  justify-content: center;  
}

.si-form__fieldset {  
  max-width: 320px;
  width: 100%;
}
.si-form__caption {
  margin-bottom: 20px;
}
.si-form__list {
  list-style-type: none;
  margin-bottom: 20px;
}


.si-form__item {
  margin-bottom: 10px;
}

.si-form__button-arrow {
  margin-bottom: 30px;
}

.si-form__wrapper-for-sign-in-button {
  height: 44px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.si-form__sign-in-text {
  color:#000;    
}

.si-form__wrapper-sign-up-button {
  width: 99px;
  height: 44px;
}